<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process139"></a>

<p><br></p>
<p><span class="rvts17">战斗的召唤（</span><span class="rvts50">Call To Arms</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:563,Y:263</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：加雷斯</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">击败亚历山大，登上复仇女士号，离开陆地</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1077.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts77">当使用净化魔杖破解了港口大门处的</span><span class="rvts20">3</span><span class="rvts21">个嚎叫者后，会和加雷斯在此处整装待发，准备战斗。</span>
<br><span class="rvts21">如果之前加雷斯不幸在废弃兵营死掉，那么在这里会和伊克斯特（</span><span class="rvts20">Exter</span><span class="rvts21">）进行对话。</span>
<br><span class="rvts21">通过对话，可以选择两名阿玛蒂亚的战士辅助战斗。可以自定义他们的职业，以补充队伍其它职业的不足。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1078.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts77">其实亚历山大比较容易解决，围墙上的骷髅射手和附魔师由于地形优势，却很难打到。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1079.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts77">当战斗进入到第三轮时，一只庞大的虚空虫子从地里钻出来。亚历山大的部队也会优先攻击这只虫子。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1080.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts77">打败大虫子和所有的亚历山大残余部队后，马拉迪出现。她会帮助解除队伍中所有人的禁锢项圈。并高称和的伙伴为神民，可以改变这个世界的命运。</span>
<br><span class="rvts21">接着，可以选择登上复仇女神号轮船进行下一张地图的开始。也可以暂时拒绝马拉迪，处理其它未处理的事宜。之后再次找到她，结束本章以及本张地图的所有内容。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



